import useCalculatePendingYield from '@/hooks/position/useCalculatePendingYield'
import useClaimPosition from '@/hooks/position/useClaimPosition'
import useCurrentPos from '@/hooks/position/useCurrentPos'
import usePositionStore from '@/store/position'
import usePositionDetailStore from '@/store/position/detail'
import { Block } from '@cetus/design'
import SingleTokenInfo from '@cetus/design/src/components/common/SingleTokenInfo'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { SingleCoinImage } from '@cetus/ui-kit'
import { d, formatCurrency, formatNumberWithDown, textEllipses } from '@cetus/utils'
import { Box, Button, HStack, Progress, Skeleton, Stack, Text, VStack } from '@chakra-ui/react'
import React, { useEffect, useMemo, useState } from 'react'
import LiquidityValueBlock from '../list/LiquidityValueBlock'

function DetailStatsInfo() {
  const { tokenAmountAfterA, tokenAmountAfterB, currentPosDetailTab, curPosContractPoolInfo } = usePositionDetailStore()
  const { currentPosBaseInfo, posFeeData, posRewardsData, posLiquidityData, currentPosBaseInfoLoading, posFeeDataLoading, posRewardsDataLoading } =
    usePositionStore()
  const { getCurrentPosBaseInfo, getCurrentPosHistory } = useCurrentPos()

  const currentPosLiquidityData = useMemo(() => {
    return posLiquidityData[currentPosBaseInfo?.posId as string]
  }, [posLiquidityData, currentPosBaseInfo?.posId])

  const currentPosFeeData = useMemo(() => {
    console.log('🚀 ~ currentPosFeeData ~ posFeeData[currentPosBaseInfo?.posId as string]:', posFeeData[currentPosBaseInfo?.posId as string])
    return posFeeData[currentPosBaseInfo?.posId as string]
  }, [posFeeData, currentPosBaseInfo?.posId])

  const currentPosRewardsData = useMemo(() => {
    console.log(
      '🚀 ~ currentPosRewardsData ~ posRewardsData[currentPosBaseInfo?.posId as string]:',
      posRewardsData[currentPosBaseInfo?.posId as string]
    )
    return posRewardsData[currentPosBaseInfo?.posId as string]
  }, [posRewardsData, currentPosBaseInfo?.posId])

  const tokenABalance = formatNumberWithDown(currentPosLiquidityData?.displayCoinAmountA, undefined, true)
  const tokenBBalance = formatNumberWithDown(currentPosLiquidityData?.displayCoinAmountB, undefined, true)

  const [totalYield, setTotalYield] = useState('--')
  const { calculatePendingYield } = useCalculatePendingYield()

  useEffect(() => {
    if (!currentPosBaseInfoLoading && !posFeeDataLoading && !posRewardsDataLoading) {
      const { total } = calculatePendingYield([currentPosBaseInfo], posFeeData, posRewardsData)
      setTotalYield(total)
    }
  }, [currentPosBaseInfoLoading, posFeeDataLoading, posRewardsDataLoading])
  const { toClaimPosition, isClaimLoading } = useClaimPosition()
  const toClaim = () => {
    toClaimPosition(currentPosBaseInfo, curPosContractPoolInfo)
  }
  const isInputValue = tokenAmountAfterA !== '' || tokenAmountAfterB !== ''

  const { isApp } = useWindowWidth()
  return (
    <Block w="100%" borderRadius="16px" p="0">
      <Block
        w="calc(100% + 2px)"
        ml="-1px"
        mt="-1px"
        bg="bg_six"
        borderRadius="16px"
        h={{ base: 'auto', lg: '80px' }}
        p={{ base: '20px 8px', lg: '20px 20px' }}
      >
        <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" justify={{ base: 'center', lg: 'space-between' }} gap={{ base: '16px', lg: '0' }}>
          <Stack
            flexDir={{ base: 'row', lg: 'column' }}
            align={{ base: 'center', lg: 'flex-start' }}
            justify={{ base: 'space-between', lg: 'center' }}
            mt={{ base: '-12px', lg: '0' }}
          >
            <Text color="primary_gray" mb={{ base: '0', lg: '-12px' }}>
              Liquidity
            </Text>
            <LiquidityValueBlock positionInfo={currentPosBaseInfo} haveTooltip={false} />
          </Stack>
          <VStack minW="200px" align={{ base: 'center', lg: 'flex-end' }} pb={{ base: '0', lg: '12px' }} gap="12px">
            <HStack w="100%" justify={{ base: 'center', lg: 'space-between' }}>
              <HStack gap="4px">
                <SingleCoinImage
                  imageUrl={currentPosBaseInfo?.displayTokenA?.logo_url}
                  w="20px"
                  h="20px"
                  showTagWidth="10px"
                  showTagHeight="10px"
                  coinType={currentPosBaseInfo ? currentPosBaseInfo?.displayTokenA?.coin_type : ''}
                />
                <Skeleton isLoaded={!!currentPosBaseInfo && !!currentPosLiquidityData}>
                  <HStack gap="4px">
                    <Text color="primary">{textEllipses(currentPosBaseInfo?.displayTokenA?.symbol)}</Text>
                    <Text color="primary">{currentPosLiquidityData?.displayPercentA}%</Text>
                  </HStack>
                </Skeleton>
              </HStack>
              <HStack gap="4px">
                <SingleCoinImage
                  imageUrl={currentPosBaseInfo?.displayTokenB?.logo_url}
                  w="20px"
                  h="20px"
                  showTagWidth="10px"
                  showTagHeight="10px"
                  coinType={currentPosBaseInfo ? currentPosBaseInfo?.displayTokenB?.coin_type : ''}
                />
                <Skeleton isLoaded={!!currentPosBaseInfo && !!currentPosLiquidityData}>
                  <HStack gap="4px">
                    <Text color="primary_green">{textEllipses(currentPosBaseInfo?.displayTokenB?.symbol)}</Text>
                    <Text color="primary_green">{currentPosLiquidityData?.displayPercentB}%</Text>
                  </HStack>
                </Skeleton>
              </HStack>
            </HStack>
            <Progress
              w="200px"
              h="4px"
              value={currentPosLiquidityData?.displayPercentA}
              bg="quote_green"
              sx={{
                'div[role="progressbar"]': {
                  bg: 'primary'
                }
              }}
            />
          </VStack>
        </Stack>
      </Block>
      <HStack
        gap={{ base: '20px', lg: '8px' }}
        p={{ base: '20px 8px', lg: '40px 8px' }}
        w="100%"
        justify="space-around"
        flexDirection={{ base: 'column', lg: 'row' }}
      >
        <TokenAmountAndAfter
          label="Base"
          token={currentPosBaseInfo?.displayTokenA}
          amount={currentPosLiquidityData?.displayCoinAmountA}
          afterAmount={tokenAmountAfterA}
        />
        <TokenAmountAndAfter
          label="Quote"
          token={currentPosBaseInfo?.displayTokenB}
          amount={currentPosLiquidityData?.displayCoinAmountB}
          afterAmount={tokenAmountAfterB}
        />
      </HStack>
      <Block
        w="calc(100% + 2px)"
        ml="-1px"
        mt="-1px"
        bg="bg_six"
        borderRadius="16px"
        h={{ base: '70px', lg: '80px' }}
        p={{ base: '20px 8px', lg: '20px' }}
      >
        <HStack w="100%" justify="space-between">
          <Stack
            flexDir={{ base: 'row', lg: 'column' }}
            alignItems={{ base: 'center', lg: 'flex-start' }}
            justify={{ base: 'space-between', lg: 'center' }}
            w={{ base: '100%', lg: 'auto' }}
          >
            <Text color="primary_gray">Pending Yield</Text>
            <Skeleton isLoaded={!!totalYield && !!currentPosBaseInfo && !!posFeeData && !!posRewardsData}>
              <Text color="text_caption" fontSize="16px">
                {formatCurrency(totalYield, 2)}
              </Text>
            </Skeleton>
          </Stack>
          <Button
            isLoading={isClaimLoading}
            disabled={Number(totalYield) == 0 || posFeeDataLoading || posRewardsDataLoading || isClaimLoading || !currentPosFeeData}
            onClick={toClaim}
            w="112px"
            h="28px"
            borderRadius="8px"
            fontWeight="500"
            fontSize="14px"
          >
            Claim
          </Button>
        </HStack>
      </Block>
      <HStack
        p={{ base: '20px 8px 0', lg: '40px 8px 20px' }}
        w="100%"
        justify="space-between"
        flexWrap="wrap"
        sx={{
          '>div': {
            mb: { base: '12px', lg: '20px' }
          }
        }}
        flexDirection={{ base: 'column', lg: 'row' }}
      >
        <TokenAmountAndAfter
          label="Fees"
          token={currentPosBaseInfo?.displayTokenA}
          amount={currentPosFeeData?.displayFeeOwedA}
          afterAmount={isInputValue ? '0' : ''}
        />
        <TokenAmountAndAfter
          label={isApp ? '' : 'Fees'}
          token={currentPosBaseInfo?.displayTokenB}
          amount={currentPosFeeData?.displayFeeOwedB}
          afterAmount={isInputValue ? '0' : ''}
        />
        {currentPosRewardsData?.map((item: any, index: number) => {
          return (
            <React.Fragment key={item?.coin_address}>
              {d(item.amount_owed).gt(0) && (
                <TokenAmountAndAfter
                  label={isApp ? (index == 0 ? 'Mining Rewards' : '') : 'Mining Rewards'}
                  token={item?.token}
                  amount={item?.display_amount_owed}
                  afterAmount={isInputValue ? '0' : ''}
                />
              )}
            </React.Fragment>
          )
        })}
      </HStack>
    </Block>
  )
}

const TokenAmountAndAfter = ({
  label,
  token,
  amount,
  afterAmount,
  isLoading
}: {
  label: string
  token: Token
  amount: string
  afterAmount: string
  isLoading?: boolean
}) => {
  const { isApp } = useWindowWidth()
  const { getExplorerUrl } = useExplorer()
  return (
    <VStack align={{ base: 'flex-end', lg: 'center' }} gap={{ base: '4px', lg: '8px' }} w={{ base: '100%', lg: 'calc(50% - 4px)' }}>
      <HStack w="100%" justify={{ base: 'space-between' }} flexDirection={{ base: 'row', lg: 'column' }}>
        <Text color="primary_gray">{label}</Text>
        <HStack>
          <Box onClick={isApp ? () => window.open(getExplorerUrl(token?.coin_type, 'coin')) : () => {}}>
            <SingleTokenInfo
              haveTooltip={!isApp}
              coinType={token ? token?.coin_type : ''}
              haveName={false}
              haveSymbol={false}
              warningIcon={{ iconW: '14px', iconH: '14px' }}
              imgBoxStyle={{ w: '28px', h: '28px' }}
            />
          </Box>
          <Skeleton isLoaded={!isLoading && !!amount}>
            <Text color="text_caption" fontSize={{ base: '14px', lg: '16px' }}>
              {formatNumberWithDown(amount)}
            </Text>
          </Skeleton>
          <Text color="text_caption" fontSize={{ base: '14px', lg: '16px' }}>
            {textEllipses(token?.symbol)}
          </Text>
        </HStack>
      </HStack>
      {afterAmount !== '' && afterAmount && (
        <HStack bg="primary_opacity.10" borderRadius="full" p=" 0 20px" h="26px" align="center">
          <Text color="primary">{formatNumberWithDown(afterAmount)}</Text>
          <Text color="primary">after</Text>
        </HStack>
      )}
    </VStack>
  )
}
export default DetailStatsInfo
