import usePosHelper from '@/hooks/position/usePosHelper'
import usePositionStore from '@/store/position'
import { AddressCopyLink, Block, TooltipIcon } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import { formatNumber } from '@cetus/utils'
import { HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import PositionCurrentPrice from '../PositionCurrentPrice'
import PositionStatus from '../PositionStatus'
import RangeValue from '../RangeValue'
import ChartBlock from './ChartBlock'

function PositionChartInfo() {
  const { currentPosBaseInfo, posPoolsRelatedData, posPoolsOriginalData } = usePositionStore()
  const currentPosPoolsRelatedData = posPoolsRelatedData[currentPosBaseInfo?.posId]

  const { getClmmPosName } = usePosHelper()
  const tokenName = useMemo(() => {
    if (currentPosBaseInfo?.tokenName) {
      return currentPosBaseInfo?.tokenName
    } else {
      const position_index = posPoolsOriginalData?.[currentPosBaseInfo?.clmmPool]?.index
      return getClmmPosName(currentPosBaseInfo?.index, position_index)
    }
  }, [currentPosBaseInfo?.tokenName, currentPosBaseInfo?.index, currentPosBaseInfo?.clmmPool, posPoolsOriginalData])

  const displayFee = currentPosPoolsRelatedData?.displayFee + '%'
  const isActive = currentPosPoolsRelatedData?.currentStatus == 'Active'

  const leverage = useMemo(() => {
    console.log('🚀 ~ leverage ~ currentPosPoolsRelatedData:', currentPosPoolsRelatedData)
    const minPrice = currentPosPoolsRelatedData?.minPriceOrigin
    const maxPrice = currentPosPoolsRelatedData?.maxPriceOrigin
    if (minPrice !== '0' && maxPrice !== '∞') {
      return formatNumber(1 / (1 - Number((minPrice / maxPrice) ** 0.25)), 2) + 'x'
    } else {
      return '1x'
    }
  }, [currentPosPoolsRelatedData?.minPriceOrigin, currentPosPoolsRelatedData?.maxPriceOrigin])
  const { getExplorerUrl } = useExplorer()
  return (
    <Block w="100%" borderRadius="20px 20px 16px 16px">
      <Block p={{ base: '16px 8px', lg: '16px' }} w="calc(100% + 34px)" ml="-17px" mt="-21px" bg="bg_six">
        <HStack justify="space-between">
          <HStack h={{ base: '40px', lg: '32px' }} gap="8px" flexDirection={{ base: 'column', lg: 'row' }}>
            <HStack w={{ base: '100%', lg: 'unset' }}>
              <Text>Position ID</Text>
              <Skeleton isLoaded={!!tokenName}>
                <Text color="text_caption">{tokenName?.split('|')[1]}</Text>
              </Skeleton>
            </HStack>
            <HStack w={{ base: '100%', lg: 'unset' }}>
              <Text>Position NFT</Text>
              <Skeleton isLoaded={!!currentPosBaseInfo?.id}>
                <AddressCopyLink
                  fontWeight="500"
                  color="text_caption"
                  fontSize="14px"
                  address={currentPosBaseInfo?.id}
                  showLink={false}
                  onClickLink={() => {
                    window.open(getExplorerUrl(currentPosBaseInfo?.id, 'nftAddress'), '_blank')
                  }}
                />
              </Skeleton>
            </HStack>
          </HStack>
          {currentPosPoolsRelatedData && (
            <PositionStatus
              isActive={isActive}
              isLoading={currentPosPoolsRelatedData?.currentStatus == ''}
              h="32px"
              p="0 12px"
              w="unset"
              isRow={true}
              imgStyle={{
                imgW: '30px',
                imgH: '16px'
              }}
            />
          )}
        </HStack>
      </Block>
      <HStack gap={{ base: '20px', lg: '8px' }} w="100%" justify="space-between" p="28px 0 20px" flexDirection={{ base: 'column', lg: 'row' }}>
        {/* <Box h="200px" /> */}
        <VStack gap={{ base: '16px', lg: '32px' }}>
          <VStack w="100%" justify="space-between" mt="8px" align={{ base: 'center', lg: 'flex-start' }}>
            <Text color="primary_gray">Current Price</Text>
            <PositionCurrentPrice
              posId={currentPosBaseInfo?.posId || ''}
              displayTokenA={currentPosBaseInfo?.displayTokenA}
              displayTokenB={currentPosBaseInfo?.displayTokenB}
            />
          </VStack>
          <VStack w="100%" justify="space-between" mt="8px" align={{ base: 'center', lg: 'flex-start' }}>
            <Text color="primary_gray">Position Range</Text>
            <RangeValue
              displayTokenA={currentPosBaseInfo?.displayTokenA}
              displayTokenB={currentPosBaseInfo?.displayTokenB}
              justify="center"
              priceInfo={{
                minPrice: currentPosPoolsRelatedData?.minPrice,
                maxPrice: currentPosPoolsRelatedData?.maxPrice,
                minPriceResever: currentPosPoolsRelatedData?.minPriceResever,
                maxPriceResever: currentPosPoolsRelatedData?.maxPriceResever
              }}
              fontSize="14px"
            />
          </VStack>
        </VStack>
        <VStack gap="0">
          <ChartBlock />
          <VStack zIndex="9">
            <HStack gap="2px" bg="primary_opacity.10" borderRadius="full" p=" 0 20px" h="26px" align="center">
              <Text color="primary" fontSize="12px">
                Leverage
              </Text>
              <TooltipIcon tooltipCon="This parameter indicates the concentration rate of your liquidity relative to a full range position." />
              <Skeleton ml="4px" isLoaded={!!leverage && !!currentPosPoolsRelatedData}>
                <Text color="primary">{leverage}</Text>
              </Skeleton>
            </HStack>
          </VStack>
        </VStack>
      </HStack>
    </Block>
  )
}
export default PositionChartInfo
